import React, { HTMLAttributes } from "react"
import styled, { css } from "styled-components"
import "github-markdown-css"

const Root = styled.div`
  &.markdown-body {
    color: ${props => props.theme.colors.neutral100};
    font-family: ${props => props.theme.font.family};

    a {
      color: ${props => props.theme.colors.primary500};
    }

    code {
      background-color: ${props => props.theme.colors.black};
    }

    hr {
      background-color: ${props => props.theme.colors.neutral050};
      border-color: ${props => props.theme.border.color};
      border-style: solid;
      border-width: 1px;
      height: 0;
      margin: 1.3em auto 1.5em;
      opacity: 0.2;
      width: calc(25% + 12px);
    }

    .highlight {
      ${({ theme: { colors } }) => {
        const background = colors.black
        const comment = colors.neutral500
        const declartion = colors.orange300
        const error = colors.red300
        const literal = colors.pink300
        const name = colors.primary400
        const string = colors.yellow300
        const text = colors.neutral050
        return css`
          background: ${background};
          color: ${text};

          .hll {
            background-color: #49483e;
          }

          .err {
            text-shadow: 0 0 7px ${error};
          }

          .ge {
            font-style: italic;
          }

          .gs {
            font-weight: bold;
          }

          .c,
          .ch,
          .cm,
          .cp,
          .cpf,
          .c1,
          .cs,
          .gu {
            color: ${comment};
          }

          .k,
          .kc,
          .kd,
          .kp,
          .kr,
          .kt,
          .no {
            color: ${declartion};
          }

          .l,
          .m,
          .mb,
          .mf,
          .mh,
          .mi,
          .mo,
          .se,
          .il {
            color: ${literal};
          }

          .n,
          .p,
          .nb,
          .ni,
          .nl,
          .nn,
          .py,
          .nv,
          .w,
          .bp,
          .vc,
          .vg,
          .vi,
          .vm {
            color: ${text};
          }

          .o,
          .gd,
          .kn,
          .nt,
          .ow {
            color: ${error};
          }

          .gi,
          .na,
          .nc,
          .nd,
          .ne,
          .nf,
          .nx,
          .fm {
            color: ${name};
          }

          .ld,
          .s,
          .sa,
          .sb,
          .sc,
          .dl,
          .sd,
          .s2,
          .sh,
          .si,
          .sx,
          .sr,
          .s1,
          .ss {
            color: ${string};
          }
        `
      }}
    }
  }
`

type MarkdownProps = HTMLAttributes<HTMLDivElement>
export default function Markdown({ className, ...props }: MarkdownProps) {
  return (
    <Root
      {...props}
      className={[className, "markdown-body"].filter(Boolean).join(" ")}
    />
  )
}
